import {Fragment, useState} from 'react';
import Breadcrumbs from '../breadcrumbs';
import {Modal} from '../modals';
import Advantages from '../advantages';
import MapContainer from '../map-container';
import PropTypes from 'prop-types';

const CreditPage = ({onOrderModalToggle, postForm}) => {
	const [showModal, setShowModal] = useState(false);
		
	return (
		<Fragment>
			<div className="container">
				<Breadcrumbs/>
			</div>			
			<div className="main credit_page">
				<section className="description_wrap">
					<div className="container">
						<h1 className="section_title">       </h1>
						<div className="description">
							<h2>  </h2>
							<p>            -.</p>
							<h2>  </h2>
							<ul>
							<li>  :  10%</li>
							<li>  :  6   5 </li>
							<li>  :  5000  2 000 000 </li>
							<li> :  10%  </li>
							<li>  2-    (,  , )</li>
							<li>    </li>
							<li>   </li>
							</ul>
							<p>*    </p>
							<p>             .</p>
						</div>
					</div>			
				</section>
				<section className="send_order">
					<div className="container">
						<div className="send_order_content">
							<h2 className="section_title">      </h2>
							<div className="send_order_text">
								           	
							</div>
							<button className="order_credit" onClick={() => setShowModal(true)}> </button>
						</div>
					</div>
					<Modal 
						id="modalCredit" 
						formTitle="      " 
						formSubtitle="           "
						submitText=" " 
						show={showModal} 
						onShowToggle={() => setShowModal(show => !show)}/>
				</section>
				<Advantages/>
				<MapContainer onModalToggle={onOrderModalToggle}/>
			</div>
		</Fragment>
	);
};

CreditPage.propTypes = {
	onOrderModalToggle: PropTypes.func
};

export default CreditPage;